.toggle {
  display: block;
  position: relative;
  float: left;
  width: 28px;
  height: 9px;
  margin-right: 20px;
  background-color: var(--white-two);
  border-radius: 5px;
  box-shadow: inset 0 0 2px rgba(0, 0, 0, 0.6);
  cursor: pointer;
}

.checkbox {
  cursor: pointer;
  display: none;
}

.circleOn {
  position: absolute;
  top: -50%;
  right: -4%;
  height: 17px;
  width: 17px;
  background-color: var(--apple);
  border-radius: 9px;
  box-shadow: inset 5px -5px 10px 5px rgba(0, 0, 0, 0.2), 0 0 4px 0 rgba(0, 0, 0, 0.7);
  color: var(--apple);
  text-align: center;
  line-height: 1;
  font-size: 1px;
}

.circleOff {
  position: absolute;
  top: -50%;
  left: -4%;
  height: 17px;
  width: 17px;
  background-color: var(--watermelon);
  border-radius: 9px;
  box-shadow: inset 5px -5px 10px 5px rgba(0, 0, 0, 0.2), 0 0 4px 0 rgba(0, 0, 0, 0.7);
  text-align: center;
  line-height: 1;
  font-size: 15px;
  font-weight: bold;
  color: var(--off-white);
}
