.text {
  font-size: 15px;
  font-weight: 500;
  margin: 0 auto;
  margin-bottom: 30px;
}

@media (min-width: 768px) {
  .text {
    font-size: 18px;
  }
}

@media (min-width: 1279px) {
  .text {
    margin: 0;
    margin-bottom: 30px;
    flex: 0 0 100%;
    max-height: 40px;
    padding-left: 60px;
  }
}
