.container {
  height: 64px;
  margin: 0 auto;
  /* padding-left: 21px; */
  display: flex;
  align-items: center;
  justify-content: space-between;
  /* box-shadow: 2px 3.5px 8px 0 rgba(29, 29, 27, 0.1); */
  background-color: var(--white);
}

.logoIcon {
  width: 24px;
  height: 18px;
  fill: var(--macaroni-and-cheese);
}

.link {
  font-family: Montserrat;
  font-size: 16px;
  font-weight: bold;
  font-style: normal;
  font-stretch: normal;
  letter-spacing: 1.28px;
  text-align: left;
  color: var(--black);
  text-decoration: none;
}

.burger {
  width: 50px;
  height: 20px;
  display: block;
  /* margin-top: 36px; */
  fill: #18191d;
}
.logOutLink {
  display: none;
  width: 0;
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    height: 80px;
    margin: 0 auto;
    flex-direction: row;
    justify-content: space-between;
    background-color: var(--white);
  }

  .border {
    box-shadow: 0px 15px 10px -15px rgba(29, 29, 27, 0.1);
  }

  .burger {
    display: none;
  }

  .link {
    display: flex;
    /* width: 90px; */
    width: 170px;
    font-size: 16px;
    height: 60px;
    align-items: center;
  }

  .logoIcon {
    fill: var(--macaroni-and-cheese);
    display: flex;
    align-items: center;
    margin: 0;
  }
  .logOutLink {
    text-decoration: none;
    display: flex;
    font-family: Montserrat;
    font-size: 14px;
    font-weight: 500;
    font-style: normal;
    font-stretch: normal;
    line-height: 1.33;
    letter-spacing: 0.18px;
    color: var(--blue-grey);
    align-self: center;
    display: unset;
    width: 18px;
  }
  .logOutLinkIcon {
    fill: var(--blue-grey);
    width: 18px;
    height: 18px;
    display: flex;
    align-self: center;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    justify-content: space-between;
  }

  .link {
    font-size: 18px;
    /* width: 100px; */
    width: 480px;
  }

  .logoIcon {
    width: 18px;
  }

  .burger {
    display: none;
  }
}
